@import "../../styles/vars/color.scss";
@import "../../styles/mixins/buttons.scss";

.formFooter{
    position: fixed;
    width: 100%;
    bottom: 0;
    left: 0;
    overflow: hidden;
    background-color: $blancoSucio;
    padding: 1rem;
    display: flex;
    justify-content: flex-end;
    box-sizing: border-box;
    align-items: center;
    -webkit-box-shadow: 0px -5px 21px 3px rgba(0,0,0,0.05);
-moz-box-shadow: 0px -5px 21px 3px rgba(0,0,0,0.05);
box-shadow: 0px -5px 21px 3px rgba(0,0,0,0.05);
    p{
        width: 33%;
        text-align: center;
    }
    button{
        width: 33%;
    }
    .back{
       @include secondaryButton;
        display: flex;
        align-items: center;
        justify-content: center;
       img {
            width: 7px;
            margin-right: 0.5rem;
        }
    }
    .next{
        @include primaryButton;
        display: flex;
        align-items: center;
        justify-content: center;
        img {
            width: 7px;
            margin-left: 0.5rem;
        }
    }
}

